import React from 'react'
import { SalaryInput } from './salaryInput'
import { Text } from '@mantine/core'
import styles from './seleryInputs.module.css'

export const SalaryInputs = () => {
  return (
    <div className={styles.wrapper}>
      <Text
        fw={700}
        size="md">
        
      </Text>
      <SalaryInput data-elem='salary-from-input' valueType={'paymentFrom'} placeholder={''}/>
      <SalaryInput data-elem='salary-to-input' valueType={'paymentTo'} placeholder={''}/>
    </div>
  )
}
